body {
    background-color: #f4f4f4;
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
  }
  
  .app {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    text-align: center;
    background: linear-gradient(135deg, #ff66cc, #5500ff);
    border-radius: 16px;
    padding: 30px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  }
  
  .quiz-title {
    color: white;
    font-size: 36px;
    font-weight: bold;
    margin-bottom: 30px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  }
  
  .question-section {
    margin-bottom: 20px;
    color: #fff;
  }
  
  .question-count {
    font-size: 22px;
    margin-bottom: 10px;
    color: #ffdd99;
  }
  
  .question-text {
    font-size: 26px;
    font-weight: 500;
    color: #ffffff;
    margin-bottom: 20px;
  }
  
  .answer-section {
    display: grid;
    gap: 15px;
    margin-top: 20px;
  }
  
  .answer-section button {
    width: 280px;
    padding: 12px;
    font-size: 18px;
    color: #fff;
    background: #ff0066;
    border: none;
    border-radius: 12px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
  }
  
  .answer-section button:hover {
    background: #ff3399;
    transform: scale(1.05);
  }
  
  .score-section {
    font-size: 28px;
    font-weight: bold;
    color: #ffffff;
  }
  